.navbar {
	position: relative;
	display: flex;
	align-items: center;
	justify-content: space-between;
	padding-top: 61px;
	z-index: 2;
	margin: 0 10%;
}
.title {
	font-size: 30px;
	font-weight: 600;
	text-decoration: none;
	color: var(--color-text);
}
.menu {
	display: flex;
}
.menuItems {
	display: flex;
	gap: 47px;
	list-style: none;
}
.menuItems a {
	text-decoration: none;
	font-size: 25px;
	color: var(--color-text);
}
.menuBtn {
	display: none;
}
@media (max-width: 830px) {
	.navbar {
		flex-direction: column;
		align-items: flex-start;
	}
	.menu {
		position: absolute;
		right: 0;
		margin-right: 10%;
		flex-direction: column;
		align-items: flex-end;
		gap: 11px;
		z-index: 3;
	}
	.menuBtn {
		display: block;
		cursor: pointer;
	}
	.menuItems {
		display: none;
		flex-direction: column;
		align-items: center;
		gap: 13px;
		border-radius: 10px;
		background: linear-gradient(
			180.11deg,
			rgba(25, 55, 109, 0.2) 0.1%,
			#19376d 99.9%
		);
		padding: 24px 33px;
		box-shadow: 0 0 4px rgba(0, 0, 0, 0.25);
	}
	.menuOpen {
		display: flex;
		z-index: 1;
	}
}
