.contactContainer {
	color: var(--color-text);
	margin-top: 120px;
	background-color: var(--color-secondary);
	display: flex;
	flex-direction: row;
	justify-content: space-around;
	width: 100vw;
	padding: 58px 10%;
	gap: 125px;
}
.text {
	white-space: nowrap;
}
.text h2 {
	font-size: 80px;
	font-weight: 700;
	letter-spacing: 4px;
}

.text p {
	font-size: 40px;
	font-weight: 400;
	letter-spacing: 2px;
}

.links {
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	list-style: none;
	gap: 26px;
}

.link {
	display: flex;
	align-items: center;
	gap: 25px;
}
.link a {
	color: var(--color-text);
	text-decoration: none;
	font-size: 15px;
	font-weight: 400;
	letter-spacing: 1.5px;
}
@media (max-width: 1100px) {
	.text p {
		font-size: 30px;
		font-weight: 400;
		letter-spacing: 2px;
	}
}
@media (max-width: 830px) {
	.contactContainer {
		flex-direction: column;
		gap: 23px;
	}
	.text {
		display: flex;
		flex-direction: column;
		align-items: center;
	}
	.links {
		align-items: flex-start;
		padding-left: 5vw;
	}
}
